import React from 'react';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import Page from './component/page.js';


const App = () =>{
    return(
      <>
      <Router>
	      <Switch>
	      <Route path = '/' component ={Page} exact ={true}/>
	      <Route render={() => <h2>Page not found :(</h2>} />
	      </Switch>
      </Router>
      </>
    )
  }

export default App;
